import React, { useState } from "react"
import Highlight, { defaultProps } from "prism-react-renderer"
import { Editor } from "react-live"
import { github } from "./themes"

const Code = React.createContext({})

export function LiveProvider({ children, theme = github, language = "html" }) {
  const [code, changeCode] = useState("")
  const [error, handleError] = useState("")
  return (
    <Code.Provider
      value={{
        error,
        code,
        onChange: changeCode,
        onError: handleError,
        theme,
        language,
      }}
    >
      {children}
    </Code.Provider>
  )
}

export function LiveEditor(props) {
  return (
    <Code.Consumer>
      {({ code, language, theme, disabled, onChange }) => (
        <Editor
          theme={theme}
          code={code}
          language={language}
          disabled={disabled}
          onChange={onChange}
          {...props}
        />
      )}
    </Code.Consumer>
  )
}

export function LivePreview(props) {
  return (
    <Code.Consumer>
      {({ onChange, ...context }) => (
        <Highlight {...defaultProps} {...context} {...props}>
          {({ className, style, tokens, getLineProps, getTokenProps }) => (
            <pre className={className + " only-preview"} style={{ ...style }}>
              {tokens.map((line, i) => (
                <div key={i} {...getLineProps({ line, key: i })}>
                  {line.map((token, key) => (
                    <span key={key} {...getTokenProps({ token, key })} />
                  ))}
                </div>
              ))}
            </pre>
          )}
        </Highlight>
      )}
    </Code.Consumer>
  )
}

export function LiveError(props) {
  return (
    <Code.Consumer>
      {({ error }) => (error ? <pre {...props}>{error}</pre> : null)}
    </Code.Consumer>
  )
}
